import React, {Component} from 'react';
import FlotChart from './FlotChart';
import Timer from './Timer';
import { options } from '../constants/charts/OptionsDefinition';
import { titleStyle } from '../constants/css-styles/main';

class MainPage extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div>
                <p style={titleStyle} >This is a React.js test !</p>
                <FlotChart options={options} />
                <Timer start={Date.now()} />
            </div>
        );
    }
}

export default MainPage;



